import Axios from 'axios'
import axiosConfig from './config'
if (Axios.defaults.baseURL == undefined) {
    axiosConfig()
}

/**
 * @function 添加资金
 */
export const money = (params) => {
    return Axios({
        method: 'post',
        url: `/money`,
        data: params
    })
}

/**
 * @function 修改时获取未转换资金
 */
export const moneyUser = (params) => {
    return Axios({
        method: 'get',
        url: `/moneyUser?id=${params.id}`,
    })
}

/**
 * @function 获取资金
 * @param {name 名字}
 * @param {type 类型}
 * @param {status 类型}
 * @param {pageNo 页}
 */
export const moneys = (params) => {
    return Axios({
        method: 'get',
        url: `/moneys?name=${params.name}&type=${params.type}&status=${params.status}&pageNo=${params.pageNo}`,
    })
}

/**
 * @function 修改资金
 */
export const moneyEdit = (params) => {
    return Axios({
        method: 'put',
        url: `/money`,
        data: params
    })
}



/**
 * @function 前台获取转换后的数据
 */
export const getMoney = (params) => {
    return Axios({
        method: 'get',
        url: `/money?id=${params.id}`
    })
};


/**
 * @function 添加投资案例
 */
export const investmentAdd = (params) => {
    return Axios({
        method: 'post',
        url: `/investment/${params.code}`,
        data: params
    })
};
/**
 * @function 获取单个资金的投资案例列表
 */
export const investments = (params) => {
    return Axios({
        method: 'get',
        url: `/investments?code=${params.code}&pageNo=${params.pageNo}`
    })
};

/**
 * @function 删除投资案例
 */
export const investmentEdit = (params) => {
    return Axios({
        method: 'delete',
        url: `/investment/${params.id}`
    })
};

/**
 * @function 名称搜索资金
 */
export const searchName = (params) => {
    return Axios({
        method: 'get',
        url: `/money/searchName?name=${params.name}`
    })
};
/**
 * @function 获取协议
 */
export const getRates = (params) => {
    return Axios({
        method: 'get',
        url: `/rates?name=${params.name}&pageNo=${params.pageNo}&status=${params.status}`
    })
};

/**
 * @function 修改协议状态
 */
export const rateEditStatus = (params) => {
    return Axios({
        method: 'put',
        url: `/rate`,
        data: params
    })
};

/**
 * @function 资金方获取确认协议的项目列表
 */
export const getTermSheetByMoney = (params) => {
    return Axios({
        method: 'get',
        url: `/termSheetByMoney?pageNo=${params.pageNo}&mmName=${params.mmName}&pmName=${params.pmName}&status=${params.status}&type=${params.type}&pageSize=${params.pageSize}`
    })
};

